<template>
  <ion-page mode="ios">
    <!-- Header Section (Blue) -->
    <ion-header mode="ios" class="header-blue">
      <div class="header-content">
        <div class="header-search">
          <ion-searchbar
            placeholder="Belajar Apa Hari ini?"
            class="search-bar"
          ></ion-searchbar>
          <ion-button class="notification-button">
            <img
              style="width: 40px; height: 30px"
              src="../assets/Alarm.png"
              alt=""
            />
          </ion-button>
        </div>
      </div>
    </ion-header>
    <!-- Main Content Section (White) -->
    <ion-content class="home-page">
      <!-- Pesan Status Non-aktif -->
    <center>
 
      <div v-if="!isUserActive" class="status-warning">
          <ion-label class="warning-text">
              Status Anda non-aktif, silakan hubungi admin.
          </ion-label>
          </div>
    </center>
      <ion-refresher slot="fixed" @ionRefresh="doRefresh($event)">
        <ion-refresher-content />
      </ion-refresher>
      <ion-content class="ion-padding">
        <h1 class="header-title">Halo {{ fullName }}</h1>
        <div class="class-info">
          <ion-label class="class-label">{{ userClass }}</ion-label>
          <ion-button class="kelas-button">Pilih Kelas</ion-button>
        </div>
        <ion-badge style="color: #fbbf24; margin-top: 12px; margin-left: 15px">
          Paket Belajar Juara Kelas!
        </ion-badge>
        <br />
        <center>
          <ion-badge class="custom-badge">
            <div class="badge-item">
              <img class="icon" src="../assets/medal.png" alt="Medal" />
              <h1 class="badge-text">0 Medal</h1>
            </div>
            <div class="badge-item">
              <img class="icon" src="../assets/hat.png" alt="Hat" />
              <h1 class="badge-text">Peringkat ke 0</h1>
            </div>
          </ion-badge>
        </center>

          <!-- Dinamis Ion Card -->
              <ion-card style="background-color: #faf8f2" :disabled="!isUserActive">
                <div class="image-grid" v-if="!loading">
                  <div v-for="item in mataPelajaran" :key="item.id">
                  <img
                    class="card-image"
                    :src="item.imageUrl"
                    :alt="item.name"
                    @click="navigateToMateri(item)"
                  />
                  <ion-label class="labell">{{ item.name }}</ion-label>
                  <ion-spinner v-if="loading" name="crescent"></ion-spinner>
                </div>
              </div>
            </ion-card>
        <!-- Dinamis Ion Card -->

        
           <!-- SNBT -->
           <img
          v-if="userClass === 'KELAS 12 SNBT'"
          style="width: 207px; margin-bottom: -5px; margin-top: 20px"
          src="../gama/SNBT.png"
          alt=""
        />
        <ion-card style="background-color: #f0f4fa; margin-top: 10px;" :disabled="!isUserActive" v-if="userClass === 'KELAS 12 SNBT' && daftarUjiansnbt.some(ujian => ujian.aktif)" >
          <div v-if="daftarUjiansnbt.length">
            <ion-grid>
              <ion-row>
                <ion-col size="6" v-for="ujian in daftarUjiansnbt" :key="ujian.id" @click="navigateToDetailUjiansnbt(ujian.id)" style="cursor: pointer;">
                  <div style="border: 1px solid #ddd; padding: 15px; border-radius: 8px; background-color: #8e44ad; margin: 5px; display: flex; flex-direction: column; align-items: center; justify-content: space-between; height: 150px;">
                    <p style="text-align: center; font-size: 16px; font-weight: bold; color: #fff; flex: 1; display: flex; align-items: center; justify-content: center;">{{ ujian.judulUjian }}</p>
                    
                    <!-- Tombol Mulai -->
                    <button style="background-color: #4CAF50; color: white; border: none; padding: 10px 20px; font-size: 14px; border-radius: 5px; cursor: pointer;">
                      Mulai
                    </button>
                  </div>
                </ion-col>
              </ion-row>
            </ion-grid>
          </div>
          <ion-spinner v-else name="crescent" style="margin: 20px auto; display: block;"></ion-spinner>
        </ion-card>
        <!-- SNBT -->

        <!-- Kedinasan -->
        <img
        v-if="userClass === 'KELAS 12 KEDINASAN'"
          style="width: 207px; margin-bottom: 10px"
          src="../gama/dinas.png"
          alt=""
        />
        <ion-card style="background-color: #f0f4fa; margin-top: 10px;" :disabled="!isUserActive" v-if="userClass === 'KELAS 12 KEDINASAN'">
          <div v-if="daftarUjian.length">
            <ion-grid>
              <ion-row>
                <ion-col size="6" v-for="ujian in daftarUjian" :key="ujian.id" @click="navigateToDetailUjian(ujian.id)" style="cursor: pointer;">
                  <div style="border: 1px solid #ddd; padding: 15px; border-radius: 8px; background-color: #3B73C5; margin: 5px; display: flex; flex-direction: column; align-items: center; justify-content: space-between; height: 150px;">
                    <p style="text-align: center; font-size: 16px; font-weight: bold; color: #fff; flex: 1; display: flex; align-items: center; justify-content: center;">{{ ujian.judulUjian }}</p>
                    
                    <!-- Tombol Mulai -->
                    <button style="background-color: #4CAF50; color: white; border: none; padding: 10px 20px; font-size: 14px; border-radius: 5px; cursor: pointer;">
                      Mulai
                    </button>
                  </div>
                </ion-col>
              </ion-row>
            </ion-grid>
          </div>
          <ion-spinner v-else name="crescent" style="margin: 20px auto; display: block;"></ion-spinner>
        </ion-card>
        <!-- Kedinasan -->


        <img
          style="width: 207px; margin-bottom: 10px"
          src="../gama/seru.png"
          alt=""
        />

        <!-- Slider di bawah gambar -->
        <ion-slides
          pager="false"
          class="custom-slider"
          :options="sliderOptions"
        >
          <ion-slide class="slider-item">
            <img src="../gama/jj.png" alt="" />
          </ion-slide>
          <ion-slide class="slider-item">
            <img src="../gama/jj.png" alt="" />
          </ion-slide>
          <ion-slide class="slider-item">
            <img src="../gama/jj.png" alt="" />
          </ion-slide>
        </ion-slides>
      </ion-content>
      <ion-grid class="ion-padding">
        <!-- Additional content for the grid can be added here -->
      </ion-grid>

      <!-- jika status nonaktif -->
      <ion-toast
      :is-open="showToast"
      message="Status Anda non-aktif, silakan hubungi admin."
      duration="3000"
      color="danger"
      position="bottom"
      @didDismiss="showToast = false"
    />
    </ion-content>
  </ion-page>
</template>
<script setup>
import {
  IonPage,
  IonContent,
  IonRefresher,
  IonRefresherContent,
  IonGrid,
  IonSlides,
  IonSlide,
  IonCard,
  IonLabel,
  IonSpinner,
  IonHeader,
  IonSearchbar,
  IonButton,
  IonBadge,
} from "@ionic/vue";
import { ref, onMounted } from "vue";
import {
  getFirestore,
  collection,
  getDocs,
  doc,
  getDoc,
} from "firebase/firestore";
import { firebaseApp } from "../views/firebase.js";
import { getAuth, onAuthStateChanged } from "firebase/auth";
import { useRouter } from "vue-router";
const router = useRouter();

const navigateToMateri = (materi) => {
  console.log("Navigating to materi:", materi);
  if (materi && materi.id && isUserActive.value) { // Hanya jika aktif
    router.push({
      name: "lihatmaterimateri",
      params: { materiId: materi.id },
    });
  } else {
    console.error("Materi atau materiId tidak ditemukan atau user tidak aktif");
  }
};

const auth = getAuth(firebaseApp);
const db = getFirestore(firebaseApp);

const fullName = ref("");
const userClass = ref("");
const mataPelajaran = ref([]);
const loading = ref(true);
const isUserActive = ref(false); // Ref untuk status aktif/non-aktif pengguna
const daftarUjian = ref([]);
const daftarUjiansnbt = ref([]);

const fetchDaftarUjian = async () => {
  try {
    const querySnapshot = await getDocs(collection(db, "jadwalkedinasan"));
    daftarUjian.value = querySnapshot.docs.map((doc) => {
      return {
        id: doc.id,
        ...doc.data(),
      };
    });
    console.log("Daftar ujian fetched:", daftarUjian.value);
  } catch (error) {
    console.error("Error fetching daftar ujian:", error);
  }
};

const fetchDaftarUjiansnbt = async () => {
  try {
    const querySnapshot = await getDocs(collection(db, "jadwalsnbt"));
    daftarUjiansnbt.value = querySnapshot.docs.map((doc) => {
      return {
        id: doc.id,
        ...doc.data(),
      };
    });
    console.log("Daftar ujiansnbt fetched:", daftarUjiansnbt.value);
  } catch (error) {
    console.error("Error fetching daftar ujian:", error);
  }
};

const navigateToDetailUjian = (jadwalId) => {
  if (isUserActive.value) { // Hanya jika aktif
    router.push({ name: "ujiankedinasan", params: { jadwalId } });
  } else {
    console.error("User tidak aktif, menu tidak tersedia");
  }
};

const navigateToDetailUjiansnbt = (jadwalId) => {
  if (isUserActive.value) { // Hanya jika aktif
    router.push({ name: "ujiansnbt", params: { jadwalId } });
  } else {
    console.error("User tidak aktif, menu tidak tersedia");
  }
};

const fetchUserData = async (uid) => {
  try {
    const userDoc = await getDoc(doc(db, "users", uid));
    if (userDoc.exists()) {
      const userData = userDoc.data();
      console.log("User data fetched:", userData);

      fullName.value = userData.fullName;
      isUserActive.value = userData.status === "aktif"; // Set status pengguna

      if (userData.kelas) {
        const classDoc = await getDoc(doc(db, "kelas", userData.kelas));
        if (classDoc.exists()) {
          const classData = classDoc.data();
          userClass.value = classData.name;
        } else {
          userClass.value = "Kelas tidak ditemukan";
          console.error("Class document not found!");
        }
      } else {
        userClass.value = "Tidak ada kelas terdaftar";
        console.log("No class ID in user data");
      }
    } else {
      console.error("User document not found!");
    }
  } catch (error) {
    console.error("Error fetching user data:", error);
  }
};

const fetchMataPelajaran = async () => {
  try {
    const querySnapshot = await getDocs(collection(db, "mataPelajaran"));
    const allData = querySnapshot.docs.map((doc) => ({
      id: doc.id,
      ...doc.data(),
    }));
    console.log("Semua data mata pelajaran:", allData);

    mataPelajaran.value = allData.filter((item) => {
      console.log(`Membandingkan ${item.kelas} dengan ${userClass.value}`);
      return item.kelas.trim().toLowerCase() === userClass.value.trim().toLowerCase();
    });
    console.log("Mata pelajaran sesuai kelas:", mataPelajaran.value);
  } catch (error) {
    console.error("Error fetching mata pelajaran:", error);
  } finally {
    loading.value = false;
  }
};




onMounted(async () => {
  onAuthStateChanged(auth, async (user) => {
    if (user) {
      await fetchUserData(user.uid); // Tunggu pengambilan data user selesai
      await fetchMataPelajaran();   // Baru ambil mata pelajaran
      await fetchDaftarUjian();     // Pastikan urutan benar
      await fetchDaftarUjiansnbt();
    } else {
      console.error("No user is logged in!");
    }
  });
});


const doRefresh = async  (event) => {
  const userId = auth.currentUser?.uid;
  if (userId) {
    fetchUserData(userId);
    fetchMataPelajaran();
  }
  event.target.complete();
};

const sliderOptions = {
  slidesPerView: 1.1,
  spaceBetween: 5,
};
</script>

<style src="../assets/style/siswa.css"></style>